import { Component, OnInit } from '@angular/core'; import { Router } from '@angular/router';  @Component({   moduleId: module.id,   selector: 'pais-dados',   templateUrl: 'pais_dados.component.html' }) export class PaisDadosComponent implements OnInit {       displayMode: PaisDadosDisplayModeEnum;     displayModeEnum = PaisDadosDisplayModeEnum;       constructor(private router: Router) { }      ngOnInit() {       const path = this.router.url.split('/')[3];       switch (path) {         case 'details':           this.displayMode = PaisDadosDisplayModeEnum.Details;           break;         case 'info':           this.displayMode = PaisDadosDisplayModeEnum.Orders;           break;         case 'edit':           this.displayMode = PaisDadosDisplayModeEnum.Edit;           break;       }     }  }  enum PaisDadosDisplayModeEnum {   Details=0,   Orders=1,   Edit=2 }


